import '../../css/Modals/Modal.css'
import React, { useContext, useEffect } from 'react'
import { useNavigate } from 'react-router-dom';
import { LoginContext } from '../../context/LoginContext';
import { FiChevronsDown, IconName } from "react-icons/fi";
import swal from 'sweetalert'


const Modal = ({info,open,close}) => {
    const navigate = useNavigate();
    
    const {isLogin} = useContext(LoginContext);
    let terms = '*            .\n *         .\n *    1234-5678\n *       (), , , , , , , , , ,\n, , , , , (, ,  ),        .'

    const infoOut = () =>{
        if (isLogin===false) {
            swal ( "" ,  "  ." ,  "error" )
        }else{
            navigate('/cart', {
                state: {
                    Prod_id : info.PROD_SEQ, 
                    imageURL : info.PROD_IMG, 
                    titleName : info.PROS_NAME, 
                    descriptionS : info.PROD_CATEGORY, 
                    prices : info.PROD_PRICE
                }
            });
            const scrollY = document.body.style.top;
            document.body.style.cssText = '';
            window.scrollTo(0, parseInt(scrollY || '0', 10) * -1);
        }
    }
    
    return (
    <div className={open ? 'openModal modal' : 'modal'}>
        {open ? (
            <section>
            <div className='modalHead'>
                <div className='headText'></div>
                <button className="close" onClick={close}>
                &times;
                </button>
            </div>
            <div className='modalMain'>
                <div className="product_area">
                    <div className="imgArea">
                        <img src={info.PROD_IMG} alt=".." className='infoImg' />
                    </div>
                    <div id="infoArea"> 
                    <div className="infoTop">
                        <p className='infoName'>{info.PROS_NAME}</p>
                        <p className='infoMaker'>{'('+info.PROD_MAKER+')'}</p>
                        <hr/>
                    </div>
                    <div className="infoPrice">
                        <span className='priceNumber'>{info.PROD_PRICE.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')}</span><span className="priceText"> </span>
                        <p className='dayPrice'> <span className='dayNumber'>{info.PROD_DAYPRICE+''}</span> 30</p>
                    </div>
                    <div className='infoCategory'>
                        <div className='taking'>
                            <div className='categoryImg'>
                                <div className='categoryBgc'>
                                    <img src='https://www.mypuzzle.co.kr/resources/images/icon/product/icn_ingest_info_cap_01.svg'></img>
                                </div>
                            </div>
                            <div className='textSuck' >
                            <p></p>
                            <p className='textFuck'>{info.PROD_INTAKE}</p>
                            </div>
                        </div>
                        <div className='func'>
                            <div className='categoryImg'>
                                <div className='categoryBgc'>
                                    <img src='https://www.mypuzzle.co.kr/resources/images/icon/health/icn_nutritional.svg'></img>
                                </div>
                            </div>
                            <div className='textSuck'>
                            <p></p>
                            <p className='textFuck'>{info.PROD_CATEGORY}</p>
                            </div>
                        </div>
                    </div>
                    <FiChevronsDown/>
                    <div className="purchase">
                        <button className='purchaseBtn' onClick={infoOut}> </button>
                    </div>
                    <div className='explanation'>
                    <p>{'-  -\n'+info.PROD_DOSE}</p>
                    <p>{'-  -\n'+info.PROD_SOBI}</p>
                    <p>{'-    -\n'+info.PROD_MATE}</p>
                    <p>{'-  -\n'+info.PROD_FUNTION}</p>
                    <p>{'-  -\n'+info.PROD_NUTRITION}</p>
                    <p>{'-   -\n'+info.PROD_CAUTION}</p>
                    <p>{'-   -\n'+info.PROD_KEEP}</p>
                    <hr/>
                    <p className='terms'>{terms}</p>
                    </div>

                    </div>
                </div>
            </div>

            </section>
        ) : null}
        </div>
    )
}

export default Modal;
