

import React from 'react'

const ReviewHead = ({count}) => {
    return (
    <div className='reviewHead'>
        <span> <span className='countNum'>{count}</span></span>
        <ul>
            <li><button></button></li>
            <li className='end'><button></button></li>
        </ul>
        <hr />
    </div>
    )
}

export default ReviewHead