import React, {useEffect} from 'react';

const Payment = ({Total}) =>{
    useEffect (()=>{
        const jquery= document.createElement('script');
        jquery.src= "https://code.jquery.com/jquery-1.12.4.min.js";
        const iamport = document.createElement('script');
        iamport.src = 'https://cdn.iamport.kr./js/iamport.payment-1.1.7.js';
        
        document.head.appendChild(jquery);
        document.head.appendChild(iamport);
        return ()=>{
            document.head.removeChild(jquery); document.head.removeChild(iamport);
        }
    }, []);

    

const Payment01 = () =>{
    const {IMP} = window;
    IMP.init("imp56415161");
    const data = {


        pg: 'html5_inicis.INIpayTest',                           // PG
        pay_method: 'card',                                     // 
        merchant_uid: 'wnsdhr7221',                           // 
        amount: Total.total,                                         // 
        name: '',                                     // 
        buyer_name: '',                                 //  
        buyer_tel: '01072417221',                             //  
        buyer_email: 'wnsdhr7221@gmail.com',                   //  
        buyer_addr: '',                              //  
        buyer_postcode: '01234',                             //  

    };
    IMP.request_pay(data, callback);
    }

    const callback = (response) => {
        const {success, error_msg, imp_uid, merchant_uid, pay_method, paid_amount, status } = response;
        if(success){
            alert('') ;
        }
        else {
            alert(` : ${error_msg}`);
        }
    }

    return(
    <>
    <button onClick={Payment01} className="checkout" style={{
        width:250,
        marginLeft:500,
        backgroundColor:'#78C1F3'
        
    }}></button>
    </>

    )
}

export default Payment;