import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import axios from 'axios';
import "../Postcss/css.css";

const EventWrite = () => {
    const navigate = useNavigate();
  
    const [title, setTitle] = useState("");
    const [name, setName] = useState("");
    const [text, setText] = useState("");
  
    const handleRegister = async () => {
        let body = {
            title: title,
            name: name,
            text: text
        };

        const response = await axios.post('http://localhost:8888/EventWrite', body);
        const newEvent = response.data.event;

        if (newEvent) {
            //      
            
            // EventLis     .
            navigate(`/EventLis`);
        }
    };

    const handleCancel = () => {
        //     

        // EventList .
        // navigate('/EventLis');
    };

    const titleHandler = (e) => {
        setTitle(e.target.value);
    };

    const nameHandler = (e) => {
        setName(e.target.value);
    };

    const textHandler = (e) => {
        setText(e.target.value);
    };

    return (
        <div className="notice_css">
            <div className="contentBox">
                <div className="board_title">
                    <strong> </strong>
                    <p>Pill.Yo   .</p>
                </div>
                <div className="board_write_wrap">
                    <div className="board_write">
                        <div className="title_name">
                            <dl>
                                <dt></dt>
                                <dd>
                                    <input type="text" placeholder=" " onChange={titleHandler} />
                                </dd>
                            </dl>
                        </div>
                        <div className="info">
                            <dl>
                                <dt></dt>
                                <dd>
                                    <input type="text" placeholder=" " onChange={nameHandler} />
                                </dd>
                            </dl>
                        </div>
                        <div className="cont">
                            <textarea placeholder="  " onChange={textHandler}></textarea>
                        </div>
                    </div>
                    <div className="bt_wrap">
                        <Link to={'/EventList'} onClick={handleRegister} className="on">
                            
                        </Link>
                        <Link to={'/EventList'} onClick={handleCancel}>
                            
                        </Link>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default EventWrite;
