import React, { useState } from 'react';
import axios from 'axios';
import '../css/Review/ReviewWrite.css';
import { useCookies } from 'react-cookie';
import { FiLink, IconName } from "react-icons/fi";
import $ from 'jquery';
import { redirect, useNavigate } from 'react-router';
import { Link } from 'react-router-dom';
window.$ = $;

const ReviewForm = () => {
    //  (form  )
  const [rating, setRating] = useState(0);
  const [username, setUsername] = useState('');
  const [comment, setComment] = useState('');
  const [image, setImage] = useState(null);
  const [cookies] = useCookies(['user_id']);
  const nav= useNavigate();
  let text = '*  Pill.Yo  .\n*             \u00A0,   ,          \u00A0.\n*      ,     \u00A0 .\n*      ,     \u00A0.'


// handle~  useState   ,     
const handleRatingChange = (value) => {
  setRating(value);
};

const handleUsernameChange = (event) => {
  setUsername(event.target.value);
};

const handleCommentChange = (event) => {
  setComment(event.target.value);
};

const handleImageChange = (event) => {
  try {
    const file = event.target.files[0];
    const imageUrl = URL.createObjectURL(file);
    setImage(imageUrl);
  } catch (error) {
    alert(' ');
  }
};
console.log(''+image);


const handleSubmit = async (event) => {
  event.preventDefault();

  let formData = new FormData();
  formData.append('userid', cookies.user_id);
  formData.append('title', username);
  formData.append('review_comment', comment);
  formData.append('review_image', image);
  formData.append('review_rating', rating);

  axios
    .post('http://localhost:8888/review/edit', formData, {
      headers: {
        'Content-Type': 'multipart/form-data',
      },
    })
    .then((response) => {
      console.log(response.data);
    })
    .catch((error) => {
      console.error(error);
    });



    setRating(0);
    setUsername('');
    setComment('');
    setImage(null);

    nav('/review');
  };
  


  return (
    <div id="form-div">
      <div className='mainSec prl'><img src="https://ifh.cc/g/qmkJwR.jpg" alt="" /><h2 style={{marginLeft:'40px'}}>Review</h2></div>
      <div className='contentBox'>
      <form className="form" id="form1" onSubmit={handleSubmit}>
          <div className="feedback">
            <div className="rating">
              <input
                type="radio"
                name="rating"
                id="rating-5"
                onChange={() => handleRatingChange(5)}
              />
              <label htmlFor="rating-5"></label>
              <input
                type="radio"
                name="rating"
                id="rating-4"
                onChange={() => handleRatingChange(4)}
              />
              <label htmlFor="rating-4"></label>
              <input
                type="radio"
                name="rating"
                id="rating-3"
                onChange={() => handleRatingChange(3)}
              />
              <label htmlFor="rating-3"></label>
              <input
                type="radio"
                name="rating"
                id="rating-2"
                onChange={() => handleRatingChange(2)}
              />
              <label htmlFor="rating-2"></label>
              <input
                type="radio"
                name="rating"
                id="rating-1"
                onChange={() => handleRatingChange(1)}
              />
              <label htmlFor="rating-1"></label>

            </div>

            <div className="name-div">
              <p><span>*</span></p>
              <input
                type="text"
                id="name"
                placeholder="   "
                value={username}
                onChange={handleUsernameChange}
                required
              />
            </div>

            <div className="comment-div">
              <p> <span>*</span></p>
              <textarea
                id="comment"
                placeholder="   "
                value={comment}
                onChange={handleCommentChange}
                required

              />
            </div>

            {/* <div className="image-div">
              <p></p>
              <input
                type="file"
                id="image"
                accept="image/*"
                onChange={handleImageChange}
              />
              {image && <img src={image} alt="Uploaded Preview"  style={{width:'200px',height:'200px'}}/>}
              </div> */}
              <div className='rvImgSec'>
              <div class="input-file-container">
                <p style={{float:'left'}}></p>
                <div class="input-file">
                  <p class="input-file__name">{image}</p>
                  <button type="button" class="input-file__button">
                    <i class="fas fa-upload"><FiLink/></i>
                  </button>
                  <input type="file" name="avatar" id="avatarInput" accept="image/*" onChange={handleImageChange}/>
                </div>
                <div className='rvImgarea'>
                {image && <img src={image} class="image-preview" alt="preview image"/>}
                </div>
              </div>
              <p>{text}</p>
              </div>
            

            <div className="submit-div">
              
              <button  className="submit-btn">
                
              </button>
              
            </div>
          </div>
      </form>
      </div>
    </div>
  );
};

export default ReviewForm;